/*_________________________ Адаптив catalog _________________________*/

/*_________________________ max 980px _________________________*/
@media(max-width: 980px) {
  .catalog {
    .container {
      padding: 0 20px;
    }

    .container-fluid {
      background-color: #f2f2f2;
    }

    &__gallery-list {
      justify-content: space-around;
    }

    &__func-container {
      gap: 50px;
      padding-left: 0;
      padding-right: 0;
    }

    &__func-buttons {
      flex-direction: column;
      gap: 10px;
    }

    &__func-btn {
      width: 100%;
    }
  }
}

/*_________________________ max 600px _________________________*/
@media(max-width: 600px) {
  .catalog {
    &__title {
      font-size: 24px;
      line-height: 33px;
    }
    &__gallery-list {
      gap: 20px;
    }

    &__gallery-img {
      width: 280px;
      height: 180px;
      object-fit: cover;
    }

    &__gallery-item {
      width: 280px;
    }

    &__func-container {
      // flex-wrap: wrap;
      flex-direction: column;
    }

    &__func-img {
      width: 280px;
      height: 374px;
      align-self: center;
    }

    &__func-buttons {
      margin-top: 50px;
    }
  }
}