/*_________________________ Header _________________________*/
.header {
  // position: relative;
  width: 100%;
  color: #fff;
  font-size: 1.8rem;
  line-height: 1.8rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: normal;
  z-index: 1;
  background-color: rgba(#000000, .3);
  height: 110px;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    padding: 26px 0 40px 0;
    margin-right: 173px;
    z-index: 3;
    &:hover {
      opacity: 0.7;
    }
  }

  &__logo-tablet {
    display: none;
  }

  &__logo-mobile {
    display: none;
  }

  &__menu {
    display: flex;
    column-gap: 79px;
    z-index: 2;
  }

  &__menu-item {
    padding: 45px 0;

    &:hover {
      color: #d22856;
    }
  }

  &__btn-area {
    padding: 10px;
    margin-right: -10px;
    z-index: 2;
    display: none;
    cursor: pointer;
  }

  &__btn {
    width: 50px;
    height: 24px;
    position: relative;
    transition: all .3s;
  }

  &__btn > span {
    display: block;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background-color: #fff;
    transition: all .3s;
  }

  &__btn > span:nth-child(2) {
    transform: translateY(-50%);
    top: 50%;

  }

  &__btn > span:nth-child(3) {
    transform: translateY(-100%);
    top: 100%;
  }

  &__btn.show > span:nth-child(2) {
    display: none;
  }

  &__btn.show > span:nth-child(1) {
    transform: translateY(600%) rotate(45deg);
  }

  &__btn.show > span:nth-child(3) {
    transform: translateY(-600%) rotate(-45deg);
  }
}

@media (max-width: 980px) {
  .header.show {
    background-color: $blue;
    transition: all 0.3s;
  }
  .header {
    &__container {
      height: 110px;
    }

    &__logo {
      padding: 13px 0;
    }

    &__logo-desktop {
      display: none;
    }
    &__logo-tablet {
      display: block;
    }

    &__menu {
      flex-direction: column;
      position: absolute;
      gap: 0;
      top: 70px;
      background-color: $blue;
      top: -600%;
      left: 0;
      right: 0;
      transition: all 0.3s;
    }

    &__menu.show {
      top: 110px;
    }

    &__menu-item {
      padding: 23px 0;
      text-align: center;
      position: relative;
      text-transform: uppercase;
    }
    &__menu-item::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 1px;
      top: 0;
      left: 0;
      right: 0;
      background-color: #000000;
      box-shadow: 0 0 1px 0 white;
    }

    &__btn-area {
      display: block;
    }

    &__btn {
      margin-right: 20px;
    }
  }
}

@media(max-width: 768px) {
  .header {
    &__logo {
      margin-right: 0;
    }
  }
}

@media(max-width: 600px) {
  .header {
    height: 60px;
    &__menu.show {
      top: 60px;
    }
    &__container {
      height: 60px;
    }

    &__logo-tablet {
      display: none;
    }

    &__logo-mobile {
      display: block;
    }
    &__btn {
      margin-right: 0;
    }
  }
}