.feedback {
  margin-bottom: 6px;
  &__title {
    font-weight: 300;
    font-size: 50px;
    line-height: 68px;
    max-width: 462px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 61px;
  }
  &__list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    max-width: 240px;
  }

  &__item-text {
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    color: #283645;
    margin-bottom: 34px;
  }
  &__item-link {
    color: $pink;
    font-weight: 300;
    font-size: 24px;
    line-height: 33px;
    text-align: center;
  }

}