/*_________________________ Page catalog _________________________*/
.page__catalog {
  grid-template-rows: min-content 1fr min-content;
}

.catalog {
  .container-fluid {
    padding: 0;
  }

  &__title {
    max-width: 360px;
  }

  &__slogan {
    max-width: 572px;
    font-size: 18px;
    line-height: 30px;
    font-weight: 400;
    margin: 100px auto;
    text-align: center;
  }

  &__panorama {
    margin-bottom: 50px;
  }

  &__panorama-photo {
    background-image: url("../img/content/panorama.webp");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    height: 506px;
  }

  &__panorama-text {
    width: 100%;
    padding: 31px 39px;
    margin: 0 auto;
    background-color: #f2f2f2;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 14px;
  }

  &__panorama-name {
    color: $blue;
    font-weight: 700;
    line-height: 24px;
    font-size: 14px;
  }

  &__panorama-time {
    font-weight: 300;
    font-size: 12px;
    text-transform: uppercase;
  }

  &__panorama-like {
    text-transform: uppercase;
    font-weight: 400;
    font-size: 12px;
    line-height: 24px;
    color: $pink;
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__panorama-blockquote {
    width: 100%;
    font-size: 14px;
    line-height: 24px;
    font-weight: 400;
    color: $blue;
  }

  &__gallery {
    margin-bottom: 67px;
  }

  &__gallery-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 54px;
    column-gap: 20px;
  }

  &__gallery-item {
    max-width: 300px;
    width: 100%;
  }

  &__gallery-img {
    width: 300px;
    height: 400px;
  }

  &__gallery-text {
    padding: 20px 27px;
    background-color: #f2f2f2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 155px;
    margin-top: -3px;
  }

  &__gallery-name {
    font-weight: 700;
    line-height: 24px;
    font-size: 14px;
    color: $blue;
  }

  &__gallery-time {
    font-weight: 300;
    font-size: 12px;
    line-height: 24px;
    color: inherit;
  }

  &__gallery-blockquote {
    font-weight: 400;
    line-height: 24px;
    font-size: 14px;
    color: $blue;
  }

  &__gallery-like {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    color: $pink;
    font-weight: 400;
    line-height: 24px;
    font-size: 12px;
    text-transform: uppercase;
    cursor: pointer;

    &:hover {
      opacity: 0.7;
    }
  }

  &__func-slogan {
    max-width: 422px;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 73px;
    font-size: 18px;
    line-height: 30px;
    font-weight: 400;
    color: $blue;
  }

  .container {
    max-width: 1200px;
    padding: 0 50px;
    margin: 0 auto;
  }
  

  &__func-container {
    background-color: #f2f2f2;
    padding: 57px 60px 57px 80px;
    display: flex;
    gap: 90px;
  }

  &__func-img {
    width: 300px;
    height: 400px;
  }

  &__func-edit {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
  }

  &__func-list {
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // gap: 50px;
  }

  &__func-item {
    border-bottom: 1px solid #28364510;
    padding-bottom: 13px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__func-item:nth-last-child(2) {
    padding-top: 15px;
  }

  &__func-item:last-child {
    padding-top: 15px;
    border-bottom: none;
  }
  

  &__func-range {
    width: 260px;
    margin: 0 30px;
    appearance: none;
    width: 100%;
    outline: none;
    background-color: $blue;
    height: 3px;
    border-radius: 10px;

    &::-webkit-slider-thumb {
      appearance: none;
      background-color: #f2f2f2;
      border: solid 3px $pink;
      width: 5px;
      height: 15px;
      border-radius: 50%;
      padding: 5px;
      cursor: pointer;
      transition: all .2s;
    }

    &::-webkit-slider-thumb:hover {
      background-color: #d22856;
    }

    &::-webkit-slider-thumb:active {
      padding: 8px;
    }
  }

.output {
  min-width: 20px;
  color: $pink;
}

  &__func-buttons {
    display: flex;
    justify-content: space-between;
  }

  &__func-btn {
    // padding: 21px 42px;
    width: 183px;
    height: 55px;
    border-radius: 30px;
    border: 3px solid $pink;
    color: $pink;
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    cursor: pointer;

    &:not(.catalog__func-btn:disabled):hover {
      color: white;
      background-color: $pink;
    }
  }

  &__func-btn:disabled {
    color: $blue;
    border-color: $blue;
    opacity: 0.1;
    cursor: auto;
  }
}