/* _______________ adaptive Index _______________ */

/* _______________ max 1200px _______________ */
@media(max-width: 1200px) {
  .container {
    padding: 0 30px;
  }

  .top {
    margin-bottom: 77px;
    height: 853px;
    padding-top: 215px;

    &__heading {
      font-size: 50px;
      line-height: 68px;
      margin-bottom: 60px;
    }

    &__button {
      margin-bottom: 75px;
    }

    &__social {
      margin-bottom: 60px;
    }
  }

  .information {
    &__list {
      margin-bottom: 76px;
    }
  }
}

/* _______________ max 980px _______________ */
@media(max-width: 980px) {
  .top {
    background-image: url(../img/content/top-img--tablet.webp);
    height: 729px;
    padding-top: 200px;
    margin-bottom: 58px;

    &__heading {
      font-size: 40px;
      font-weight: 300;
      line-height: 120%;
      text-align: center;
      max-width: 240px;
      margin: 0 auto;
      margin-bottom: 39px;
    }

    &__button {
      margin-bottom: 50px;
      font-size: 18px;
      line-height: 1.5;
    }

    &__social {
      margin-bottom: 42px;
    }

    &::after {
      height: 729px;
    }
  }

  .page__body {
    .offer-title {
      font-size: 4rem;
      margin-bottom: 10px;
      line-height: 1.5;
    }
  }

  .information {
    padding-top: 50px;
    margin-bottom: 100px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 439px auto;
    column-gap: 10px;
    position: relative;
    background-position: 0 5%;
    background-size: 100% 57.4%;

    &__container-cards {
      grid-column: 1 / -1;
      display: flex;
      gap: 42px;
    }

    &__container-timer {
      grid-column: 1 / -1;
      margin-top: -1px;
    }

    &__cards {
      display: none;
    }

    &__cards-tablet {
      display: inline-block;
      overflow: hidden;
      width: 268px;
      height: 547px;
      flex-grow: 0;
      flex-shrink: 0;
    }

    &__list {
      margin-top: 0;
      margin-bottom: 0;
      flex-direction: column;
      justify-content: flex-start;
      gap: 30px;
      padding-top: 40px;
    }

    &__item {
      box-shadow: none;
      width: auto;
      padding-top: 0;
    }

    &__item-title {
      font-size: 18px;
      line-height: 24px;
      text-align: left;
      margin: 0 0 10px 0;
    }

    &__item-text {
      font-size: 14px;
      line-height: 24px;
      text-align: left;
      margin: 0;
    }

    &__timer {
      grid-column: 1 / -1;
      background-color: #fff;
      padding-top: 37px;
      margin-left: 0;
    }

    &__timer-block-image {
      margin-bottom: 0;
    }

    &__timer-image {
      width: 238px;
      height: 276px;
    }

    &__timer-title {
      margin-bottom: 24px;
    }

    &__timer-text {
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 24px;
    }

    &__timer-link {
      font-size: 14px;
      line-height: 24px;
    }
  }

  .price {
    padding: 126px 0 70px 0;

    margin-bottom: 50px;
    position: relative;

    &__th {
      width: 152px;
      height: 75px;
      font-size: 16px;
      line-height: 30px;
    }

    &__th:nth-child(3)::after {
      top: 9px;
      left: -40%;
      font-size: 12px;
      line-height: 1.5;
    }

    &__th-cost {
      font-size: 18px;
      line-height: 30px;
    }

    &__td {
      width: 153px;
      height: 45px;
      font-size: 14px;
      line-height: 30px;
    }
  }

  .feedback {
    &__list {
      row-gap: 50px;
    }

    &__title {
      font-size: 40px;
      line-height: 54px;
      margin-bottom: 50px;
    }

    &__item:nth-of-type(2) {
      max-width: 100%;
      width: 100%;
      order: 1;
    }

    &__item-text {
      margin-bottom: 17px;
    }
  }

  .map {
    height: 374px;
    background: url(../img/map-triangle.webp) 50% 0 / 100% 70px no-repeat, url(../img/map2.webp) 0 0/ cover no-repeat;
  }
}

/* _______________ max 600px _______________ */
@media(max-width: 600px) {
  .container {
    padding: 0 20px;
  }
  .top {
    background-image: url(../img/content/top-img--mobile3.webp);
    height: 833px;
    padding-top: 130px;
    margin-bottom: 40px;
    
    &__heading {
      font-size: 24px;
      line-height: 1.2;
      max-width: 100%;
      margin-bottom: 400px;
    }

    &__container {
      margin: 0 auto;
      max-width: 100%;
    }

    &__button {
      width: 100%;
      margin-bottom: 30px;
    }

    &__social {
      margin: 0 20px 20px 20px;
    }

    &__text {
      font-size: 14px;
      line-height: 1.7;
    }
  }

  .page__body .offer-title {
    font-size: 2.4rem;
    margin-bottom: 25px;
  }
  .page__body .offer-title br {
    display: none;
  }
  .information {
    padding-top: 140px;
    margin-bottom: 0;
    grid-template-columns: 1fr;
    grid-template-rows: min-content;
    row-gap: 30px;
    background-size: cover;

    &__container-cards {
      flex-wrap: wrap;
    }

    &__cards-tablet {
      width: 157px;
      height: 320px;
      margin: 0 auto;
      order: 1;
      z-index: 0;
      margin-bottom: -30px;
    }

    &__list {
      gap: 50px;
    }

    &__item {
      border: none;
    }

    &__item-title {
      text-align: center;
    }

    &__item-text {
      text-align: center;
    }

    &__timer {
      display: none;
    }
  }

  .comments {
    padding-top: 50px;
    margin-top: -41px;
    z-index: 2;
    background-color: #fff;

    &__container {
      padding: 0 10px;
      display: flex;
      flex-direction: column;
    }

    &__img {
      display: none;
    }

    &__info {
      display: none;
    }

    &__author {
      font-size: 18px;
      order: -1;
    }

    &__text {
      font-size: 14px;
    }


    .slider {
      &__item {
        padding: 20px;
        max-height: 300px;
        overflow-y: auto;
      }

      &__control {
        width: 10px;
      }
    }
  }


  .price {
    background-size: cover;
    margin-top: 20px;
    margin-bottom: 40px;
    padding-top: 160px;
    padding-bottom: 90px;

    &__table {
      display: none;
    }

    &__table--mobile {
      display: block;
    }
    .container-fluid__table {
      padding: 0;
    }
    .slider {
      &__item {
        padding: 0;
      }

      &__item:nth-of-type(2) .price__th {
        position: relative;
        overflow: hidden;

        &::after {
          position: absolute;
          content: 'ХИТ';
          background-color: $pink;
          font-size: 14px;
          line-height: 25px;
          font-weight: 400;
          top: 15px;
          left: -45%;
          transform: rotate(-45deg);
          width: 100%;
        }
      }
    }

    &__th {
      width: 100vw;
      height: 94px;
      font-size: 16px;
    }

    &__td {
      text-transform: uppercase;
      display: flex;
      width: 100%;
      align-items: center;
      justify-content: space-between;
      padding: 28px;
    }

    &__th-cost {
      font-size: 18px;
    }

    &::after {
      height: 420px;
    }
  }

  .feedback {
    margin-bottom: 20px;
    &__title {
      font-size: 24px;
      line-height: 1.5;
      letter-spacing: .2rem;
      margin-bottom: 45px;
    }

    &__item {
      max-width: 100%;
      width: 100%;
      letter-spacing: .2rem;
    }
  }
  .map {
    background: url(../img/map-triangle.webp) 50% 0 / 100% 40px no-repeat, url(../img/map2.webp) 0 0/ cover no-repeat;
  height: 200px;
  position: relative;
  }
}