
.price {
  background-image: url(../img/grey-background.webp);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
  background-size: 100% 100%; 
  padding: 156px 0 98px 0;
&__table--mobile {
  display: none;
}

  padding-bottom: 98px;
  margin-bottom: 74px;

  &__table {
    width: 100%;
    border-collapse: collapse;
  }

  &__th {
    border: 1px solid #e5e5e5;
    background-color: $blue;
    text-align: center;
    color: white;
    width: 233px;
    height: 94px;
    text-transform: uppercase;
    font-size: 18px;
    line-height: 30px;
    font-weight: 700;

    &-cost {
      font-weight: 300;
      line-height: 30px;
      font-size: 24px;
      margin-top: 15px;
    }
  }

  &__th:nth-child(3) {
    position: relative;
    overflow: hidden;

    &::after {
      position: absolute;
      content: 'ХИТ';
      background-color: $pink;
      font-size: 14px;
      line-height: 25px;
      font-weight: 400;
      top: 15px;
      left: -40%;
      transform: rotate(-45deg);
      width: 100%;
    }
  }

  &__td {
    border: 1px solid #e5e5e5;
    width: 233px;
    // width: 25%;
    height: 59px;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
    vertical-align: middle;
    background-color: #fff;
  }

  &__td:nth-child(1) {
    padding-left: 22px;
    text-align: left;
  }

  &__td-img-no {
    width: 27px;
    height: 27px;
  }

  &__td-img-yes {
    width: 31px;
    height: 21px;
  }
}
