/*_________________________ Footer _________________________*/
.footer {
  padding: 62px 0;
  
  &__container {
    color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }

  &__logo {
    &:hover{
      opacity: 0.7;
    }
  }
  &__logo-tablet {
    display: none;
  }
  &__social {
    display: flex;
    gap: 15px;
  }

  &__social-item {
    padding: 20px;
    border: 3px solid #2836451a;
    border-radius: 50%;
    &:hover {
      background-color: #2836451a;
    }
    .svg-youtube {
      margin-left: 1px;
      margin-right: -1px;
      margin-top: 1px;
      margin-bottom: -1px;
    }
  }

  &__copy-link {
    font-size: 1.8rem;
    color: $pink;
    border: 3px solid #2836451a;
    padding: 10px 20px;
    border-radius: 7px;
    cursor: pointer;
    text-align: center;
    &:hover {
      background-color: #2836451a;
    }
  }
}

@media(max-width: 980px) { 
  .footer {
    padding: 27px 0;
    &__social-item {
      padding: 17px;
    }
    &__logo-desktop {
      display: none;
    }
    &__logo-tablet {
      display: block;
    }
  }
}

@media(max-width: 650px) {
.footer {
  padding: 40px 0;
  height: auto;
  &__container {
    flex-direction: column;
    row-gap: 40px;
  }
}
}