/* _______________ adaptive Contest _______________ */

@media(max-width: 980px) {
  .contest {
    &__form-wrapper {
      flex-direction: column;
      gap: 50px;
    }
    &__form-name {
      width: 100%;
      gap: 50px;
    }
    &__form-name-label {
      justify-content: flex-start;
    }
    &__form-name-input {
      width: 100%;
      max-width: 100%;
    }
    &__form-travel {
      width: 100%;
    }
    &__form-achivements {
      flex-direction: column;
      flex-wrap: nowrap;
      max-height: min-content;
    }
    &__form-contact {
      flex-direction: column;
      gap: 41px;
    }
    &__form-contact-label {
      width: 100%;
    }
    &__form-contact-input {
      max-width: 100%;
    }
  }
}

@media(max-width: 600px) {
  .contest {
    &__title {
      font-size: 24px;
      line-height: 33px;
    }
    &__form-wrapper {
      margin-bottom: 50px;
    }
    &__form-name {
      gap: 30px;
    }
    &__form-name-label {
      flex-direction: column;
      gap: 10px;
      span {
        align-self: flex-start;
        margin-left: 20px;
      }
    }

    &__form-travel, &__form-achivements {
      padding-top: 120px;
      border: none;
      background-color: #f2f2f2;
      position: relative;
    }
    &__form-travel-legend, &__form-achivements-legend {
      position: absolute;
      top: 40px;
      left: 0;
      right: 0;
      text-align: center;
    }
    &__form-travel-label {
      margin-left: 70px;
    }
    &__form-contact {
      border: none;
      padding: 0;
      gap: 25px;
      &-legend {
        display: none;
      }
    }
    &__form-contact-label {
      gap: 10px;
    }
    &__form-contact-input {
      order: 1;
    }
    &__form-achivements {
      padding: 130px 0px 40px 70px;
    }
    &__form-emo {
      border: none;
      padding: 10px;
      margin-bottom: 50px;
    }
    &__form-emo-area {
      height: 197px;
    }
    &__form-footer {
      flex-direction: column;
      gap: 40px;
      margin-bottom: 25px;
    }
    &__form-footer-btn {
      width: 100%;
    }
  }
}