.comments {
  text-align: center;
  margin-bottom: 41px;
  &__container {
    padding: 0 30px;
  }
  &__img {
    width: 79px;
    height: 63px;
    margin-bottom: 58px; 
  }
  &__text {
    font-weight: 400;
    line-height: 30px;
    font-size: 18px;
    max-width: 688px;
    width: 100%;
    margin: 0 auto;
    color: $blue;
    margin-bottom: 50px;
  }
  &__author {
    font-weight: 700;
    line-height: 30px;
    font-size: 24px;
    color: #000;
    margin-bottom: 17px;
  }
  &__info {
    font-weight: 400;
    line-height: 30px;
    font-size: 18px;
  }
  .slider__item {
    max-height: 350px;
    overflow: auto;
  }
}