/*_________________________ Global styles _________________________*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.page {
  font-size: 10px; 
}

a {
  color: inherit;
  display: inline-block;
  text-decoration: none;
}

ul {
  list-style: none;
}

.body {
  background-color: #fff;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  color: #283645;
  overflow-x: hidden;
  display: grid;
  grid-template-rows: 1fr min-content;
  height: 100vh;
}

::-webkit-scrollbar {
width: 5px;
 background-color: #28364575;
 border-radius: 1rem;
}

::-webkit-scrollbar-thumb {
  background-color: #d2285675;
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #d22856;
}

.container {
  max-width: 1200px;
  padding: 0 50px;
  margin: 0 auto;
}

.container-fluid {
  width: 100%;
  max-width: 100vw;
  padding: 0 20px;
}

@media (max-width: 980px) {
  .container {
    padding: 0 20px;
  }
}

.main__top-bg {
  background-image: url("../img/content/bg-desktop.webp");
  background-repeat: no-repeat;
  background-position: 50% 0%;
  background-size: cover;
  padding-top: 185px;
  padding-bottom: 104px;
  margin-top: -110px;

  &-title {
    color: white;
    font-size: 50px;
    line-height: 68px;
    font-weight: 300;
    text-align: center;
    margin: 0 auto;
  }
}