.top {
  background-image: url(../img/content/top-img--desktop.webp);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: 100% 0;
  height: 1200px;
  width: 100vw;
  margin-top: -110px;
  padding: 300px 0 0 0;
  margin-bottom: 100px;
  &__container {
    max-width: 300px;
  }

  background-color: bisque;

  &__heading {
    color: #fff;
    font-weight: 300;
    font-size: 60px;
    line-height: 88px;
    text-align: center;
    margin-bottom: 90px;
    letter-spacing: .2rem;
  }

  &__button {
    width: 300px;
    height: 56px;
    border: 3px solid #fff;
    border-radius: 29px;
    background-color: transparent;
    font-weight: 700;
    font-size: 21px;
    line-height: 18px;
    color: #fff;
    margin-bottom: 80px;
    cursor: pointer;

    &:hover {
      color: $pink;
      border-color: $pink;
    }
  }

  &__social {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 50px;
    margin-bottom: 70px;
  }

  &__social-item {
    cursor: pointer;

    &:hover {
      opacity: 0.7;
    }
  }
&__social-item::marker {
  font-size: 0;
}
  &__text {
    color: #fff;
    font-weight: 300;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
  }
}

.image {
  z-index: -1;
  position: relative;
  top: -694px;
  right: -150px;

  &__main {
    position: absolute;
    top: 0;
    right: 0;
  }

  &__screen {
    position: absolute;
    top: 64px;
    right: 250px;
  }
}

