/* _______________ Contest _______________ */ 
.page__contest {
  grid-template-rows: min-content 1fr min-content;
}

.contest {

  input[type='text'] {
    outline: none;

    &:focus {
      outline: 2px solid $lightblue;
    }
  }

  &__top-bg {
    margin-bottom: 100px;
  }

  &__title {
    max-width: 747px;
  }

  &__slogan {
    max-width: 756px;
    margin: 0 auto;
    font-size: 18px;
    line-height: 30px;
    font-weight: 400;
    color: $blue;
    text-align: center;
    margin-bottom: 70px;
  }

  &__form-wrapper {
    display: flex;
    gap: 74px;
    margin-bottom: 70px;
  }

  &__form-name {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: none;
    width: 50%;
    padding-top: 15px;
  }

  &__form-name-label {
    font-size: 14px;
    line-height: 30px;
    font-weight: 400;
    text-transform: uppercase;
    color: $pink;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }

  &__form-name-label span {
    min-width: 80px;
  }

  &__form-name-input {
    border: 2px solid #e5e5e5;
    border-radius: 1px;
    padding: 17px;
    max-width: 400px;
    width: 100%;
    font-size: 18px;
    color: $blue;
  }

  &__form-travel {
    display: flex;
    flex-direction: column;
    width: 50%;
    border: 2px solid #e5e5e5;
    padding: 30px 0 15px 0;

  }

  &__form-legend {
    margin: 0 auto;
    padding: 0 20px;
    font-size: 14px;
    line-height: 30px;
    font-weight: 400;
    color: $pink;
    text-transform: uppercase;
  }

  &__form-travel-label {
    font-weight: 400;
    line-height: 30px;
    font-size: 18px;
    color: $blue;
    margin-left: 125px;
    margin-bottom: 40px;
    margin-right: auto;
    cursor: pointer;
  }

  &__form-travel-radio {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    padding-left: 20px;
  }

  &__form-travel-span {
    position: absolute;
    width: 30px;
    height: 30px;
    border: 2px solid #e5e5e5;
    margin-left: -45px;
    border-radius: 50%;
  }


  &__form-travel-radio:checked+&__form-travel-span::before {
    content: '';
    width: 12px;
    height: 12px;
    background-color: $pink;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
  }

  &__form-travel-radio:checked+&__form-travel-span {
    border-color: $pink;
  }

  &__form-travel-radio:focus+&__form-travel-span {
    outline: 2px solid $lightblue;
  }


  &__form-contact {
    width: 100%;
    border: 2px solid #e5e5e5;
    padding: 44px 60px 15px;
    display: flex;
    gap: 74px;
    margin-bottom: 64px;
  }

  &__form-contact-label {
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    font-size: 14px;
    line-height: 30px;
    font-weight: 400;
    color: $pink;
    text-transform: uppercase;
  }

  &__form-contact-label span {
    margin-left: 20px;
  }

  &__form-contact-input {
    border: 2px solid #e5e5e5;
    border-radius: 1px;
    padding: 17px;
    padding-right: 65px;
    max-width: 450px;
    width: 100%;
    font-size: 18px;
    color: #283645;
    background-image: url(/img/phone-input.webp);
    background-position: center right;
    background-repeat: no-repeat;
    &:focus {
      outline: 2px solid $lightblue;
    }
  }

  &__form-contact-label:nth-of-type(2) &__form-contact-input {
    background-image: url(/img/mail-input.webp);
    background-position: center right;
    background-repeat: no-repeat;
  }

  &__form-achivements {
    margin-bottom: 64px;
    padding: 50px 60px 40px 100px;
    border: 2px solid #e5e5e5;
    max-height: 250px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 40px 70px;
  }

  &__form-achivements-label {
    font-weight: 400;
    line-height: 30px;
    font-size: 18px;
    max-width: 200px;
    cursor: pointer;
  }

  &__form-achivements-checkbox {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    padding-left: 20px;
  }

  &__form-achivements-span {
    position: absolute;
    width: 35px;
    height: 35px;
    border: 2px solid #e5e5e5;
    border-radius: 3px;
    margin-left: -45px;
  }


  &__form-achivements-checkbox:checked+&__form-achivements-span::before {
    content: url(/img/tick.webp);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

  }

  &__form-achivements-checkbox:focus+&__form-achivements-span {
    outline: 2px solid $lightblue;
  }

  &__form-emo {
    border: 2px solid #e5e5e5;
    padding: 45px 60px;
    margin-bottom: 70px;
  }

  &__form-emo-area {
    border: 2px solid #e5e5e5;
    resize: none;
    padding: 22px 20px;
    width: 100%;
    font-size: 18px;
    line-height: 30px;
    font-weight: 400;
    color: $blue;
    min-height: 125px;
    outline: none;
    &:focus {
      outline: 2px solid $lightblue;
    }
  }
  &__form-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-transform: uppercase;
  }
  &__form-footer-btn {
    text-transform: inherit;
    padding: 23px 55px;
    border-radius: 3px;
    border: 2px solid #2aab6d;
    color: #ffffff;
    background-color: #2aab6d;
    cursor: pointer;
    &:hover {
      opacity: .9;
    }
    &:focus {
      outline: 2px solid $lightblue;
    }
  }
  &__form-footer-text {
    span {
      color: $pink;
      font-weight: 700;
    }
  }
}