.offer-title {
  margin-bottom: 66px;
  margin-top: 10px;
  font-weight: 300;
  font-size: 50px;
  line-height: 68px;
  text-align: center;
}

.information {
  margin-bottom: 72px;
  width: 100vw;
  background-image: url(../img/grey-background.webp);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
  background-size: 100% 100%;
  z-index: -1;



  &__cards-tablet {
    display: none;
  }

  &__cards {
    display: flex;
    justify-content: space-around;
  }

  &__card {
    position: relative;
  }

  &__card-img {
    width: 186px;
    height: 382px;
  }

  &__card-screen {
    position: absolute;
    width: 165px;
    height: 294px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__list {
    display: flex;
    justify-content: space-around;
    margin-bottom: 158px;
    margin-top: -90px;
  }

  &__item {
    border-top: #000;
    width: 300px;
    padding-top: 60px;
    background-color: #f2f2f2;
    z-index: 5;
    border-top: 5px solid transparent;
    box-shadow: 0px -15px 10px -20px rgba(0, 0, 0, 0.9);
  }

  &__item-title {
    margin: 0 30px;
    font-size: 30px;
    line-height: 30px;
    font-weight: 700;
    text-align: center;
    color: #000;
  }

  &__item-text {
    margin: 35px 24px 0 24px;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
  }

  &__timer {
    display: flex;
    align-items: center;
    margin-left: 35px;
  }

  &__timer-block-image {
    margin: 17px 0 67px 0;
  }

  &__timer-image {
    width: 298px;
    height: 345px;
  }

  &__timer-description {
    margin-left: 70px;
  }

  &__timer-title {
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    margin-bottom: 37px;
  }

  &__timer-text {
    margin-bottom: 47px;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
  }

  &__timer-link {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: $pink;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
}